.post-feed .post {
  display: flex;
  padding: 35px 0 33px;
  position: relative;
}
.post-feed .post + .post {
  border-top: 1px solid var(--mid-gray-color);
}
.post-feed .post-link:hover ~ .post-header .post-title-link {
  color: var(--primary-color);
}
.post-feed .post-media {
  flex-shrink: 0;
  margin-bottom: 0;
  width: 200px;
}
.post-feed .post-wrapper {
  margin-left: 30px;
}
.post-feed .post.no-image .post-wrapper {
  margin-left: 0;
}
@media (max-width: 767px) {
  .post-feed .post {
    display: block;
  }
  .post-feed .post-media {
    margin-bottom: 30px;
    width: auto;
  }
  .post-feed .post-wrapper {
    margin-left: 0;
  }
}
