:root {
  --primary-color: #6c5ce7;
  --primary-text-color: #333;
  --secondary-text-color: #999;
  --red-color: #dc3545;
  --orange-color: #ffc107;
  --green-color: #28a745;
  --white-color: #fff;
  --lighter-gray-color: #f7f7f7;
  --light-gray-color: #f1f1f1;
  --mid-gray-color: #e6e6e6;
  --dark-gray-color: #1a1a1a;
  --black-color: #000;
  --font-base: Roboto, sans-serif;
  --animation-base: ease-in-out;
  --navbar-height: 70px;
}
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  background-color: var(--white-color);
  color: var(--primary-text-color);
  font-family: var(--font-base);
  font-size: 16px;
  line-height: 1.7;
  text-rendering: optimizeLegibility;
  word-break: break-word;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  color: var(--dark-gray-color);
  text-decoration: none;
  transition: color 0.1s var(--animation-base);
}
a:hover {
  color: var(--primary-color);
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--dark-gray-color);
  font-weight: 700;
  line-height: 1.3;
  margin: 0 0 15px;
}
p {
  margin: 0 0 20px;
}
img {
  display: block;
  height: auto;
  max-width: 100%;
}
blockquote {
  color: var(--secondary-text-color);
  font-size: 22px;
  font-weight: 500;
  line-height: 1.45;
  margin: 45px 60px;
}
blockquote strong {
  font-weight: 700;
}
table {
  border-collapse: collapse;
  font-size: 15px;
  margin: 30px 0;
  width: 100%;
  word-break: normal;
}
thead {
  background-color: var(--lighter-gray-color);
  border-bottom: 1px solid var(--mid-gray-color);
}
tr + tr {
  border-top: 1px solid var(--mid-gray-color);
}
tr:hover td {
  background-color: var(--lighter-gray-color);
}
th,
td {
  line-height: 1.5;
  padding: 10px 15px;
}
th {
  color: var(--secondary-text-color);
  font-weight: 500;
  text-align: left;
}
tfoot {
  border-top: 1px solid var(--mid-gray-color);
}
tfoot td {
  font-weight: 700;
}
ul,
ol {
  margin: 30px 0;
  padding-left: 20px;
}
iframe,
embed {
  border: 0;
  display: block;
  overflow: hidden;
  width: 100%;
}
figure {
  margin: 45px 0;
}
figcaption {
  color: var(--dark-gray-color);
  font-size: 14px;
  margin-top: 15px;
  text-align: center;
}
pre {
  margin: 30px 0;
  overflow-x: scroll;
}
code {
  font-size: 14px;
}
hr {
  background-color: var(--secondary-text-color);
  border: 0;
  border-radius: 50%;
  height: 5px;
  margin: 45px auto;
  position: relative;
  width: 5px;
}
hr:before,
hr:after {
  background-color: var(--secondary-text-color);
  border-radius: 50%;
  content: "";
  height: 5px;
  position: absolute;
  width: 5px;
}
hr:before {
  right: 20px;
}
hr:after {
  left: 20px;
}
@media (max-width: 767px) {
  blockquote {
    margin-left: 0;
    margin-right: 0;
  }
  .table thead {
    display: none;
  }
  .table tr {
    display: block;
    padding: 12px 0;
  }
  .table td {
    display: flex;
    justify-content: space-between;
    padding: 7px 0;
    text-align: right;
    width: 100%;
  }
  .table td:before {
    color: var(--secondary-text-color);
    content: attr(data-label);
    margin-right: 10px;
    white-space: nowrap;
  }
}
